import { Card, Flex, Title, Text, Avatar } from "@vkontakte/vkui";
import "./ProfileView.css";

const ProfileView = ({ user, totalTimeSpent, totalTasksCreated }) => {
  if (!user) return null;

  const getUserRating = () => {
    if (totalTasksCreated < 15) return "";
    if (totalTasksCreated >= 15 && totalTasksCreated < 40) return ""; 
    if (totalTasksCreated >= 40 && totalTasksCreated < 75) return "";
    if (totalTasksCreated >= 75 && totalTasksCreated < 150) return "";
    if (totalTasksCreated >= 150 && totalTasksCreated < 250) return "";
    if (totalTasksCreated >= 250 && totalTasksCreated < 400) return " ";
    if (totalTasksCreated >= 400 && totalTasksCreated < 600) return " ";
    if (totalTasksCreated >= 600 && totalTasksCreated < 800) return " ";
    if (totalTasksCreated >= 800 && totalTasksCreated < 1000) return " ";
    return " ";
  };

  const getRatingColor = () => {
    const colors = {
      "": "#94A3B8",
      "": "#38BDF8",
      "": "#818CF8",
      "": "#C084FC",
      "": "#FB923C",
      "": "#34D399",
      "": "#F97316",
      "": "#6366F1",
      " ": "#EF4444",
      " ": "#DB2777"
    };
    return colors[getUserRating()] || "#38BDF8"; 
  };

  const formatTime = (seconds) => {
    const hours = Math.floor(seconds / 3600);
    const minutes = Math.floor((seconds % 3600) / 60);
    if (hours > 0) {
      return `${hours}  ${minutes}  ${seconds} `;
    }
    return `${minutes}  ${seconds} `;
  };

  return (
    <Card mode="shadow" className="profile-card">
      <Flex direction="column" align="center" gap="m">
        <Avatar src={user.photo_200} size={96} />
        <Title level="1" className="profile-name">
          {user.first_name} {user.last_name}
        </Title>

        <Flex direction="column" align="center" gap="s">
          <Text
            weight="2"
            style={{
              color: getRatingColor(),
              fontSize: "18px",
              textTransform: "uppercase",
              letterSpacing: "1px",
            }}
          >
            {getUserRating()}
          </Text>
          <Text
            weight="3"
            style={{
              color: "#718096",
              fontSize: "14px",
            }}
          >
             : {totalTasksCreated}
          </Text>
        </Flex>

        <Flex direction="column" align="center" gap="s">
          <Text
            weight="3"
            style={{
              color: "#718096",
              fontSize: "14px",
            }}
          >
              : {formatTime(totalTimeSpent)}
          </Text>
        </Flex>
      </Flex>
    </Card>
  );
};

export default ProfileView;
